.modal {
  background-color: rgba(0,0,0,0.3);
  width: 100vw;
  height: 100vh;
  position: fixed;
  z-index: 2;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 1;
  transition: opacity 1s ease-in-out;
}

.modal_hidden {
  opacity: 0;
  transition: opacity 1s ease-in-out;
}

.modal__container {
  width: 496px;
  height: 436px;
  border-radius: 20px;
  background-color: #fff;
  border: 1px solid rgba(0, 0, 0, 0.3);
  position: relative;
}

.modal__container-alt {
  width: 498px;
  height: 610px;
}

.modal__container-delete {
  width: 670px;
  height: 280px;
}

.modal__close {
  background: url('../images/close.svg');
  width: 14px;
  height: 14px;
  background-position: center;
  border-style: none;
  position: absolute;
  left: 451px;
  top: 23px;
  cursor: pointer;
}

.modal__close-delete {
  position: absolute;
  left: 633px;
  top: 23px;
}

.modal__header {
  font-size: 16px;
  margin-left: 28px;
  margin-top: 28px;
}

.modal__image {
  width: 498px;
  height: 498px;
  object-fit: cover;
  border-radius: 20px 20px 0 0;
}

.modal__title {
  font-size: 20px;
  line-height: 24px;
  margin: 20px auto 12px 32px;
}

.modal__desc {
  font-size: 20px;
  line-height: 24px;
  margin: 0 auto 32px 32px;
}

.modal__delete {
  border-style: none;
  background-color: rgba(0,0,0,0);
  color: #FF4D00;
  position: absolute;
  bottom: 68px;
  right: 32px;
  font-size: 20px;
  line-height: 24px;
  cursor: pointer;
}

.modal__title-delete {
  text-align: center;
  width: 367px;
  height: 48px;
  margin: 60px auto 172px auto;
}

.modal__confirmation {
  border-style: none;
  background-color: rgba(0,0,0,0);
  color: #FF4D00;
  position: absolute;
  top: 148px;
  font-size: 20px;
  line-height: 24px;
  cursor: pointer;
  width: 100%;
}

.modal__cancel {
  border-style: none;
  background-color: rgba(0,0,0,0);
  color: #000;
  position: absolute;
  bottom: 60px;
  left: 305px;
  font-size: 20px;
  line-height: 24px;
  cursor: pointer;
}